.message {
    width: 575px;
    height: 77px;
    display: flex;
    flex-direction: column;
    /* padding-left: 70px; */
    margin: 25px 0 0 530px;
    padding-top: 25px;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    /* justify-content: center; */
    padding-left: 10px;
}

.imageAndText img {
    width: 48px;
    height: 48px;
    margin-left: 24px;
    
}

.text {
    min-width: 400px;
    height: 60px;
    outline: 1px solid red;
    background-color: rgba(0, 102, 204, 1);
    color: white;
    border-radius: 10px 10px 0 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
}

.text:after {
    content: '';
}

.name {
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    font-weight: 600;
    line-height: 19.5px;
    padding: 0 15px 3px 0;
    align-self: flex-end;
}

.messageText {
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 19.5px;
    padding-left: 15px;
    
}

.time {
    font-family: 'Montserrat', sans-serif;
    font-size: 10px;
    font-weight: 600;
    line-height: 12.19px;
    width: 26px;
    height: 12px;
    align-self: flex-end;
    margin-right: 10px;
}