.button {
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    width: 130px;
    height: 30px;
    padding: 3px 24px 5px 24px;
    border-radius: 3px;
}

.disabled {
    background-color: rgba(0, 77, 153, 0.503);
}

.secondary {
    background-color: white;
    border: 1px solid rgba(0, 102, 204, 1);
    color: rgba(0, 102, 204, 1);
    width: 175px;
}

.default {
    background-color: rgba(0, 102, 204, 1);
    color: white;
    border:  rgba(0, 102, 204, 1);
}

.red {
    background-color: rgba(204, 20, 57, 1);
    color: white;
    border: rgba(204, 20, 57, 1);
}

.default:hover {
    cursor: pointer;
    width: 165px;
    background-color: rgba(0, 128, 255, 1);

}

.red:hover {
    cursor: pointer;
    width: 165px;
    background-color: rgba(255, 26, 71, 1);

}

.secondary:hover {
    cursor: pointer;
    width: 165px;
    background-color: rgba(229, 240, 250, 1);

}


.default:active {
    width: 150px;
    background-color: rgba(0, 89, 178, 1);
}

.secondary:active {
    width: 150px;
    background-color: rgba(201, 229, 255, 1);
}

.red:active {
    width: 150px;
    background-color: rgba(178, 18, 50, 1);
}

.button:disabled {
}
