.greetingForm {
    width: 1280px;
    height: 372px; 
}

.inputAndButtonContainer {
    display: flex;
    gap: 12px;
}

.error {
    margin-top: 7px;
    width: 165px;
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 17.07px;
    color: rgba(204, 0, 0, 1);
}

.input {
    width: 372px;
    height: 36px;
    border-radius: 5px;
    border: 1px solid rgba(209, 209, 209, 0.558);
}

.input:focus {
    border-color: rgba(209, 209, 209, 0.558);
}

.errorInput {
    border: 1px solid rgba(204, 0, 0, 1);
    width: 372px;
    height: 36px;
    border-radius: 5px;
}

.button {
    width: 78px;
    height: 36px;
    top: 1024px;
    left: 454px;
    padding: 5px 24px 5px 24px;
    gap: 10px;
    border-radius: 3px 0px 0px 0px;
    opacity: 0.5px;
    background: rgba(0, 102, 204, 1);
    color: white;
    cursor: pointer;
}

.button:focus {
}

.button:active {
    background: rgba(0, 102, 204, 1);
}

.button:disabled {
    background: rgba(0, 102, 204, 0.403);
}

.text {
    width: 143px;
    height: 17px;
    margin: 0 0 10px 0;
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 17.07px;
    color: rgba(0, 0, 0, 0.5);
}

.greeting {
    width: 144px;
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    font-weight: 500;
    line-height: 17.07px;
}